.tm-main{
    width: 100%;
    display: flex;
    border-radius: 10px;
    background: white;
    justify-content: center;

}
.tm-head > p {
    font-size: 30px;
    font-weight: 700;
    color: rgb(0, 140, 50);
    /* background: linear-gradient(to right,rgb(0, 140, 50), white);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text; */
  }

@media screen and (max-width: 700px) {
    .tm-main{
        height: 100%;
        border-radius: 0px;
    }
    .tm-head{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 5px 20px 1px #00000020;

    }
    .tm-head > p {
        margin: 0;
    }

}